// Variables
@import 'variables';
@import 'animate.css';

body{
    margin: 0;
    font-family: "Inter Tight", sans-serif !important;
    color: #1E3B6B;

    fieldset {
        border: none;
        padding: 0;
        padding: 0;
    }
}

form .work-visa-message{
    display: none;
}

footer{
    color: black;
}


.bold{
    font-weight: 700;
}

.bolder{
    font-weight: 900;
}

.gold{
    color: $brand_gold;
}

.title-subtitle{
    text-align: center;

    .title{
        font-size: 35px;
        margin-bottom: 4px;

        @media (max-width:400px) {
            font-size: 28px;
        }
    }

    .subtitle{
        font-size: 18px;
    }
}

#section-anchor-links-element{
    position: fixed;
    // top: 20px;
    top: 0;
    right: 0;
    left: 0;
    // background: transparent linear-gradient(90deg, rgba(0,0,0,0) 32%, #E7D7B6 100%) 0% 0% no-repeat padding-box;
    display: none;
    z-index: 3;

    // padding-top: 20px;
    // padding-bottom: 20px;
    background: white;
    background: linear-gradient(180deg, rgb(255, 255, 255) 0%, rgb(255, 255, 255) 80%, rgba(255, 255, 255, 0) 100%);

    .wrapper{
        background: transparent linear-gradient(90deg, rgba(0,0,0,0) 32%, #E7D7B6 100%) 0% 0% no-repeat padding-box;
    }

    .links-wrapper{
        display: flex;
        gap: 40px;
        justify-content: flex-end;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 14px;
        // padding-top: 3px;
        // padding-bottom: 3px;

        @media (max-width:500px) {
            gap: 5px;
        }

        @media (max-width:360px) {
            font-size: 13px;
        }

        @media (max-width:345px) {
            font-size: 12px;
        }

        .link{
            padding: 6px;
            color: $blue_bg;
            transition: 0.2s all;

            &:hover{
                color: #f0e3c9;
                background: $blue_bg;
                cursor: pointer;
            }

            &.active{
                color: #f0e3c9;
                background: $blue_bg;
            }
        }
    }
}

#learn-more-cta{
    position: fixed;
    right: 0;
    // right: 10px;
    top: 20%;
    z-index: 5;
    background: $blue_bg;
    color: white;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    width: 90px;
    box-shadow: -3px 1px 15px -3px rgba(0, 0, 0, 0.35);
    overflow: hidden;
    transition: 0.2s all;

    &.hidden{
        opacity: 0;
        pointer-events: none;

        &:hover{
            cursor: unset;
            opacity: 0;
        }
    }

    .fixed-cta-overlay{
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
    }

    @media (max-width:540px) {
        display: none;
    }

    &:hover{
        cursor: pointer;
        opacity: 0.9;
    }

    .image-container{
        // height: 70px;
    }

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .text{
        font-size: 13px;
        padding: 8px 8px;
        line-height: 1.2;
        text-align: center;
        font-weight: 500;
    }
}

#header-one{
    padding: 24px 0;

    @media (max-width:450px) {
        padding: 18px 0;
    }

    .container{
        @media (max-width:500px) {
            padding-left: 25px;
            padding-right: 25px;
        }
    }

    #header-logo{
        width: 200px;

        @media (max-width:450px) {
            width: 140px;
        }
    }

    .right-header-column{
        text-align: right;

        @media (max-width:768px) {
            text-align: unset;
        }
    }

    .header-line-break{
        display: none;

        @media (max-width:500px) {
            display: block;
        }
    }

    .text-container{
        @media (max-width:768px) {
            display: none;
        }
    }
}

.header-row{
    justify-content: space-between;
    align-items: center;

    @media (max-width:768px) {
        // row-gap: 20px;
        row-gap: unset;
    }

    @media (max-width:450px) {
        // row-gap: 30px;
    }

    .text-container{

        .text{
            font-size: 30px;
            // font-weight: 900;
            font-weight: 800;
            color: $brand_gold;
            margin: unset;

            @media (max-width:450px) {
                // font-size: 23px;
                font-size: 28px;
                line-height: 1.3;
            }
        }
    }
}

.usa-performance .outside-header{
    display: none;
}

// As we're integrating the header within the banner, we need to have its height in order to compensate the banner's height
#banner-video-section.performance-page-banner{
    height: calc(70vh + 112px);

    .header-row .text-container{

        .text{
            color: white;
        }

        .subtitle{
            color: white;
        }

    }

    .usa-performance header .right-header-column .subtitle{
        color: white;
    }

    #form-container-element.visible-on-load{
        top: calc(25px + 112px);
    }
}

.header-in-banner{
    background: transparent;
    position: absolute;
    top: 0;
    z-index: 1;
    right: 0;
    left: 0;
    width: 100%;
}

#banner-video-section{
    height: 70vh;
    position: relative;

    @media (max-width:500px) {
        height: 60vh;
    }

    .video-container.image-container{
        position: relative;

        .banner-text-slider{
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            background: linear-gradient(180deg, rgba(112, 112, 112, 0) 0%, rgb(59 59 59) 100%);

            @media (max-width:600px) {
                padding-top: 60px;
            }

            // Individual item of the slider
            .text-item{

                padding-bottom: 50px;
                padding-left: 50px;
                padding-right: 50px;

                // Inner wrapper for the item's content
                .item-content{
                    width: 50%;
                    // text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
                    // text-shadow: 4px 3px 12px black;
                    text-shadow: 4px 3px 12px rgba(0, 0, 0, 0.55);

                    @media (max-width:1060px) {
                        width: 40%;
                    }

                    @media (max-width:768px) {
                        width: 100%;
                    }

                    .title{
                        font-size: 50px;
                        // text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
                        font-weight: 700;
                        line-height: 1.1;
                        color: white;

                        @media (max-width:991px) {
                            font-size: 40px;
                        }

                        @media (max-width:600px) {
                            font-size: 32px;
                        }

                        @media (max-width:480px) {
                            font-size: 28px;
                        }

                        @media (max-width:400px) {
                            font-size: 26px;
                        }
                    }

                    .subtitle{
                        font-size: 24px;
                        color: #DFD7BB;

                        @media (max-width:480px) {
                            font-size: 22px;
                        }

                        @media (max-width:400px) {
                            font-size: 18px;
                        }
                    }
                }
            }
        }
    }

    .video-container{
        border-bottom-left-radius: 50px;
        border-bottom-right-radius: 50px;
        overflow: hidden;
        height: 100%;
    }

    video, img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .desktop-banner-asset{
        display: block;
    }

    .mobile-banner-asset{
        display: none;
    }

    @media (max-width:768px) {

        .desktop-banner-asset{
            display: none;
        }

        .mobile-banner-asset{
            display: block;
        }
    }

    .discover-how-today{
        background: $blue_bg;
        position: absolute;
        bottom: -24px;
        right: 0;
        left: 0;
        width: fit-content;
        margin: auto;
        color: white;
        padding: 12px 40px;
        border-radius: 34px;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: 1px;
        box-shadow: 0px 0px 30px #F2CA78;
        z-index: 1;

        &:hover{
            background: $brand_gold;
            cursor: pointer;
        }

        @media (max-width:576px) {
            display: none;
        }
    }

    .double-down-chevron{
        position: absolute;
        bottom: 40px;
        right: 40px;
        cursor: pointer;

        @media (max-width:500px) {
            right: 10px;
        }

        @media (max-width:370px) {
            bottom: 30px;
        }

        svg{
            width: 60px;
            height: 60px;
            fill: white;

            animation: fade-slide-up 1s 1s ease-out forwards,
            pulse 2s 3s ease-out infinite;

            path{
                filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 1));
            }

            @keyframes pulse {
                0% {
                  opacity: 1;
                  transform: none;
                }
                50% {
                  opacity: .8;
                  transform: scale(.8);
                }
                100% {
                  opacity: 1;
                  transform: none;
                }
              }
        }
    }

    .fading-text{
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 80px;
        margin: auto;
        height: fit-content;
        width: fit-content;
        text-align: center;
        // display: none;

        --animate-duration: 5s;
        --animate-delay: 0.5s;


        .top-fade{
            // display: none;
        }

        .bottom-fade{
            // display: none;
        }

        @media (max-width:768px) {
            // display: block;
            --animate-delay: 3s;
        }

        h2{
            font-weight: 900;
            margin: 0;
            font-size: 38px;

            @media (max-width:768px) {
                font-size: 28px;
            }
        }
    }

}

#benefits-of-living-in-portugal-section{

    // margin: 70px 0 50px 0;
    padding-top: 70px;
    padding-bottom: 50px;
    position: relative;

    @media (max-width:768px) {
        padding-top: 40px;
    }

    .container{
        position: relative;
        z-index: 2;
    }

    .benefits-grid{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        // gap: 40px;
        gap: 110px;
        // padding-bottom: 450px;
        padding-bottom: 380px;

        @media (max-width:991px) {
            padding-bottom: 230px;
            gap: 40px;
        }

        @media (max-width:540px) {
            grid-template-columns: 1fr;
            padding-bottom: 135px;
        }

        @media (max-width:420px) {
            padding-bottom: 100px;
        }

        .item{
            display: flex;
            flex-direction: column;
            align-items: center;
            row-gap: 15px;
            color: $blue_bg;
            font-size: 24px;
            text-align: center;

            @media (max-width:768px) {
                font-size: 18px;
                row-gap: 10px;
            }

            @media (max-width:540px) {
                span{
                    width: 65%;
                }
            }

            .icon{
                height: 100px;

                @media (max-width:540px) {
                    height: 80px;
                }
            }
        }
    }

    .background-container{
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 100%;

        border-bottom-right-radius: 50px;
        border-bottom-left-radius: 50px;
        overflow: hidden;

        @media (max-width:540px) {
            // top: 50%;
            // height: 50%;

            height: unset;
            top: unset;
        }

        .overlay-element{
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 58%, rgba(255, 255, 255, 0) 100%);
        }
    }

    .bg-image{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

}

#investment-visa-options-section{

    // background: linear-gradient(180deg, rgb(255, 255, 255) 0%, rgb(255, 255, 255) 60%, rgb(237 224 198 / 50%) 100%);
    padding-bottom: 40px;
    padding-top: 60px;
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;

    @media (max-width:768px) {
        padding-bottom: unset;
    }

    .title-container{
        text-align: center;

        h1{
            color: $brand_gold;
            font-weight: 900;
        }
    }

    .table-container{
        @media (max-width:400px) {
            padding-left: 0;
            padding-right: 0;
        }
    }

    .visa-options-table{

        border-radius: 15px;
        // overflow: hidden;
        font-weight: 500;
        margin-top: 80px;
        font-size: 18px;

        @media (max-width:991px) {
            font-size: 16px;
        }

        @media (max-width:500px) {
            .table-row{
                // padding-left: 10px;
                // padding-right: 10px;
            }
        }

        @media (max-width:440px) {
            font-size: 14px;
            margin-top: 70px;
        }

        .title{
            font-weight: 900;
            max-width: calc(100% / 3);

            // &.odd{
            //     background: #e5dccc;
            // }
        }

        .table-row:nth-child(even) .title{
            background: #e5dccc;
        }

        .table-row:nth-child(odd) .title{
            background: #B7853C;
        }

        .program-name{
            font-weight: 700;
            padding-top: 35px;
        }

        .table-cell{
            min-width: unset;
        }

        .table-row{
            align-items: center;
            flex-wrap: nowrap;
        }

        .table-row:nth-child(even){
            background: #F3EBDA;
        }

        .table-row:nth-child(odd){
            background: $brand_gold;
            color: white;
        }

        .header{
            // padding-top: 25px;

            .table-cell{
                position: relative;
            }

            .visa-program-image{
                position: absolute;
                left: 0;
                right: 0;
                width: 80px;
                margin: auto;
                bottom: 100%;
                top: -30px;

                @media (max-width:420px) {
                    width: 60px;
                }
            }
        }

        .span3{
            flex:2;
        }
    }

    // Revamped table
    .revamped-table-container{

        .visa-options-table{

            width: 85%;
            margin: auto;
            margin-top: 80px;

            @media (max-width:991px) {
                width: 100%;
            }

            .table-row:not(.header){
                @media (max-width:500px) {
                    display: grid;
                    grid-template-columns: 25% 1fr 1fr;
                }
            }

            .table-row{
                column-gap: 20px;
                border: unset;

                @media (max-width:600px) {
                    column-gap: 10px;
                }

                &:last-of-type{

                    .table-cell:not(:first-of-type){
                        border-bottom: 4px solid $brand_gold;
                        border-bottom-right-radius: 20px;
                        border-bottom-left-radius: 20px;
                    }
                }
            }

            .header{

                .program-name{
                    padding: 0;
                }

                .program-name-element{
                    display: flex;
                    justify-content: center;
                    background: red;
                    // padding-top: 8px;
                    // padding-bottom: 8px;

                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    font-size: 26px;

                    @media (max-width:991px) {
                        font-size: 22px;
                    }

                    @media (max-width:768px) {
                        font-size: 18px;
                    }

                    @media (max-width:460px) {
                        font-size: 16px;
                    }

                    .item{
                        color: white;
                        padding-top: 6px;
                        padding-bottom: 6px;

                        &:first-of-type{
                            background: $brand_gold;
                            // text-align: end;

                            span{
                                // margin-right: 14px;
                            }
                        }

                        &:last-of-type{
                            background: $blue_bg;
                            // text-align: start;

                            span{
                                // margin-left: 14px;
                            }
                        }

                        span{
                            display: block;
                        }
                    }
                }

            }

            .no-border{
                border-right: 4px solid $blue_bg;
                border-left: 4px solid $brand_gold;
                border-top-right-radius: 20px;
                border-top-left-radius: 20px;
                overflow: hidden;
            }

            .table-cell:not(.title):not(.no-border) {
                border-left: 4px solid $brand_gold;
                border-right: 4px solid $brand_gold;
            }

            .table-row:nth-child(odd){
                // background: #F3EBDA;
                background: white;
                color: $blue_bg;

                .title{
                    background: unset;
                }
            }

            .table-row:nth-child(even){
                // background: white;
                background: #F3EBDA;
                background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(243, 235, 218, 1) 28%, rgba(243, 235, 218, 1) 100%);

                .title{
                    background: unset;
                    background: linear-gradient(94deg, rgba(255, 255, 255, 1) 0%, rgba(243, 235, 218, 1) 50%);
                }
            }

            .table-cell{
                font-weight: 800;
                font-size: 20px;

                @media (max-width:991px) {
                    font-size: 15px;
                }

                &.title{
                    font-weight: 500;
                    font-size: 18px;
                    text-align: right;

                    @media (max-width:991px) {
                        font-size: 14px;
                    }

                    @media (max-width:500px) {
                        max-width: 25%;
                    }
                }
            }

            .program-name{
                padding: 18px 8px;
            }
        }
    }
}

#ph-selling-points-section{

    .container{
        // padding: 60px 0;
        padding-top: 60px;
        padding-bottom: 60px;
    }

    .title-subtitle{
        margin-bottom: 40px;
    }

    .selling-points{

        // justify-content: center;
