#testimonials-section{

    background: linear-gradient(180deg, rgba(231, 215, 182, 0.6) 0%, rgba(255, 255, 255, 1) 60%, rgba(255, 255, 255, 1) 100%);
    border-top-right-radius: 50px;
    border-top-left-radius: 50px;

    .container{
        padding-top: 60px;
        padding-bottom: 60px;

        @media (max-width:500px) {
            padding-left: 25px;
            padding-right: 25px;
        }
    }

    // .title-container{
    //     text-align: center;
    // }

    .discover-how-today-with-face{
        background: $blue_bg;
        position: relative;
        // bottom: -24px;
        // right: 0;
        // left: 0;
        width: fit-content;
        margin: auto;
        color: white;
        padding: 14px 40px;
        padding-left: 95px;
        border-radius: 34px;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: 1px;
        box-shadow: 0px 0px 30px #F2CA78;
        // margin-top: 50px;
        margin-top: 35px;

        &:hover{
            background: $brand_gold;
            cursor: pointer;
        }

        @media (max-width:576px) {
            position: fixed;
            left: 0;
            right: 0;
            z-index: 10;

            width: 100%;
            bottom: 0;
            border-radius: inherit;
            text-align: center;
            font-size: 22px;

            .wrapper{
                position: relative;
            }
        }

        @media (max-width:500px) {
            font-size: 20px;
        }

        @media (max-width:360px) {
            font-size: 18px;
        }

        .agent-face-wrapper{
            position: absolute;
            height: 80px;
            width: 80px;
            border-radius: 100%;
            top: 0;
            bottom: 0;
            left: 0;
            margin: auto;
            overflow: hidden;

            img{
                width: 100%;
                height: 100%;
            }

            @media (max-width:576px) {
                bottom: 25px;
            }

            @media (max-width:540px) {
                left: -25px;
            }

            @media (max-width:500px) {
                width: 70px;
                height: 70px;
            }

            @media (max-width:440px) {
                left: -40px;
            }

            @media (max-width:410px) {
                left: -65px;
            }

            @media (max-width:370px) {
                width: 60px;
                height: 60px;
                bottom: 30px;
            }
        }
    }

    .call-back-text{
        text-align: center;
        display: block;
        margin-top: 10px;
        font-size: 14px;

        @media (max-width:578px) {
            display: none;
        }
    }

    .testimonials-row{
        justify-content: center;
        // margin-top: 40px;
        column-gap: 20px;

        @media (max-width:991px) {
            column-gap: unset;
        }

        @media (max-width:576px) {
            display: none;
        }
    }

    .testimonial-col{
        @media (max-width:991px) {
            width: calc(100%/3);
        }
    }

    .testimonials-content{

        .testimonial-card{
            border: 3px solid #C3922E;
            background: white;
            display: flex;
            gap: 14px;
            padding: 16px;
            height: 240px;
            align-items: center;

            &.expanded{
                .read-more{
                    display: none;
