    .generic-form{
        display: flex;
        flex-direction: column;
        row-gap: 12px;
        margin-top: 20px;

        input, select, textarea{
            box-shadow: inset 0px 3px 6px #00000029;
            border: none;
            // padding: 10px 14px;
            padding: 7px 14px;
            color: inherit;
            border-radius: 6px;

            &:focus{
                box-shadow: 0 0 3px #c3922e;
                outline: 1px solid #d9ab4d;
                /* border: 1px solid #d9ab4d; */
            }
        }

        .submit-button{
            width: fit-content;
