    .close-button{
        position: absolute;
        top: 20px;
        right: 20px;
        font-size: 28px;

        &:hover{
            cursor: pointer;
        }
    }

    .checkbox-wrapper {
        width: 100%;
        // margin: 20px auto 15px auto;
        // margin-top: 20px;
        margin-top: 5px;
        position: relative;
        // display: block;
        display: flex;

        .stylize {
            width: auto;
            opacity: 0.00000001;
            position: absolute;
            left: 0;
            margin-left: -20px;
        }

        label:before {
            content: '';
            position: absolute;
            left: 0;
            // top: 0;
            /* margin-left: 4px; */
            width: 26px;
            height: 26px;
            transition: transform 0.28s ease;
            border-radius: 3px;
            border: 1px solid $brand_gold;
            background: white;
        }

        label:after {
            content: '';
            display: block;
            width: 12px;
            height: 7px;
            border-bottom: 2px solid #c3922e;
            border-left: 2px solid #c3922e;
            -webkit-transform: rotate(-45deg) scale(0);
            transform: rotate(-45deg) scale(0);
            transition: transform ease 0.25s;
            will-change: transform;
            position: absolute;
            // top: 2px;
            top: 7px;
            left: 7px;
        }

        .stylize:checked~label::before {
            color: #c3922e;
        }

        .stylize:checked~label::after {
            -webkit-transform: rotate(-45deg) scale(1);
            transform: rotate(-45deg) scale(1);
        }

        .stylize:checked~label:before {
            border: 2px solid #c3922e;
        }

        label {
            position: relative;
            min-height: 34px;
            display: initial;
            padding-left: 36px;
            margin-bottom: 0;
            font-weight: normal;
            cursor: pointer;
            vertical-align: sub;
            line-height: 18px;
            font-size: 14px;
            line-height: 1.1;
        }
    }

    .consent-text{
        // font-size: 13px;
        font-size: 11px;
        line-height: 1.2;
        color: rgba(30, 59, 107, 0.7019607843) ;
    }
}

