                border-radius: 10px;
                -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.2);
                background-color: $brand_mid_golden;
            }
        }

        .meeting-item {
            padding: 20px;
            transition: 0.2s all;
            transition: 0.2s all;

            &:not(.active):hover{
                cursor: pointer;
            }

            .row{
                align-items: center;
            }

            .date {
                display: block;
                font-size: 18px;
                color: #1e3b6b; /* Example color */
                margin-bottom: 10px;
            }

            .city {
                display: block;
                font-size: 24px;
                color: #1e3b6b; /* Example color */
                // font-weight: bold;
                font-weight: 600;
            }

            .row>.col-md-6{
                width: 50%;
            }

            .city-info{
                // display: none;

                .meeting-date{
                    font-size: 16px;
                }
            }

            .book-button{
                display: none;
            }

            &.active{

                background: $brand_gold;
                color: white;

                .city {
                    font-size: 28px;
                    font-weight: bold;
                    color: white;
                }

                .city-info{
                    display: block;

                    .book-button{
                        display: flex;
                        background: #1E3B6B;
                        border: none;
                        border-radius: 10px;
                        color: white;
                        padding: 8px 26px;
                        font-size: 13px;
                        letter-spacing: 1px;
                        transition: 0.2s all;
                        font-weight: 500;
                        border-radius: 20px;
                        margin: auto;
                        margin-top: 10px;

                        &:hover,
                        &:focus{
                            opacity: 1.2;
                        }
                    }

                    .meeting-date{
                        font-size: 20px;
                    }
                }

                // .date, .book-button{
                //     display: block;
                // }

            }
        }

        .book-button {
            padding: 10px 20px;
            background-color: #003366; /* Example color */
            color: white;
            border: none;
            cursor: pointer;
            margin-top: 10px;
            transition: 0.2s all;

            &:hover{
                filter: brightness(1.3);
            }
        }
    }
}

