// Notifications Element
#notifications {
    cursor: pointer;
    position: fixed;
    right: 0px;
    z-index: 9999;
    bottom: 0px;
    margin-bottom: 22px;
    margin-right: 15px;
    // max-width: 330px;
    max-width: 350px;

    @media (max-width:400px) {
        max-width: 90%;
    }

    .alert {
        position: relative;
        // padding: 1.4rem 2.2rem;
        // padding: 1.4rem 1.8rem;
        padding: 15px 20px;
        font-weight: 500;
        margin-bottom: 1rem;
        border: 1px solid transparent;
        border-radius: 0.25rem;
        border-radius: 10px;
        color: white;
    }

    .alert-success {
        background-color: $brand_light_golden;
        border-color: $brand_light_golden;
    }

    .alert-error {
        // background-color: red;
        background-color: #f4403b;
        // border-color: red;
        border-color: #f4403b;
    }

    .close {
        float: right;
        font-size: 1.5rem;
        font-weight: 700;
        line-height: 1;
        color: #000;
        text-shadow: 0 1px 0 #fff;
        opacity: .5;
    }

    .info-icon {
        padding-right: 5px;
        font-size: 16px;
        position: relative;
        top: 1px;
    }

    a {
        font-weight: 900;
        color: white;
    }
}

